      let yy = date.getFullYear();
      let mm = date.getMonth() + 1;
      let dd = date.getDate();
      return yy + "/" + mm + "/" + dd;
    },
    mpks() {
      const num_mpks = this.isGHP61 ? 3 : 6;
      const arr = [
        [365, 366, 368, 367, "icon_mpk1"],
        [369, 370, 372, 371, "icon_mpk2"],
        [373, 374, 376, 375, "icon_mpk3"],
        [377, 378, 380, 379, "icon_mpk4"],
        [381, 382, 384, 383, "icon_mpk5"],
        [385, 386, 388, 387, "icon_mpk6"],
      ];
      return arr.slice(0, num_mpks);
    },
    mapIcon() {
      const obj = {};
      this.$nextTick(() => {
        this.mpkList.forEach((v) => {
          obj[v.icon] = v.description;
        });
      });
      return obj;
    },
  },
  methods: {
    saveHtml() {
      localStorage.setItem("editorHtml", this.editorHtml);
      localStorage.setItem("pages", this.room_no);
      this.saveMpkList();
    },
    saveMpkList() {
      localStorage.setItem("selectMpk", JSON.stringify(this.selectMpk));
      localStorage.setItem("table", JSON.stringify(this.table));
      localStorage.setItem("locale", this.locale);
      this.$message.success(this.$t("DATA_IS_SAVE_OK"));
    },
    setEditorHtml(h) {
      this.editorHtml = h;
    },
    // 添加icon
    AddIcon(i, none) {
      if (none) return false;
      this.selectedIcon = this.selectMpk[i].icon || "";
      this.editXML = i;
      this.dialogVisible = true;
    },
    // 保存选择的icon
    saveMPK() {
      this.dialogVisible = false;
      this.selectMpk[this.editXML].icon = this.selectedIcon;
      this.selectMpk[this.editXML].description =this.mapIcon[this.selectedIcon];
    },
    getBatchDoc() {
      if (this.room_no) {
        const reg1 = /^(\d+-)(\d+)$/g; // 规则xxx-xxx的数字
        const reg2 = /^(\d+)[\d+|,]+(\d+)$/g; // 规则 xxx,xxx,xx的数字
        const result1 = reg1.test(this.room_no);
        const result2 = reg2.test(this.room_no);
        if (result1 || result2) {
          const doc = this.getDoc();
          const section = document.createElement("section");
          let page = [];
          const room = document.querySelector(".room-no");
          if (!room) {
            return this.$message.error(this.$t("NO_MARK"));
          }

          if (this.room_no.includes("-")) {
            let arr = this.room_no.split("-");
            let start = parseInt(arr[0]);
            let end = parseInt(arr[1]);
            let num = arr[0].length;
            for (let i = start; i <= end; i++) {
              page.push((i + "").padStart(num, "0"));
            }
          } else if (this.room_no.includes(",")) {
            page = this.room_no.split(",");
          } else {
            page = [];
          }
          const target = room.innerText.replace(/[^0-9]/gi, "");

          if (page.length > 0) {
            let allDoc = "";
            page.forEach((p) => (allDoc = allDoc + doc.replaceAll(target, p)));
            section.innerHTML = allDoc;
          }
          this.section = section;
          this.$message.success(this.$t("VAR_SET_OK"));
        } else {
          this.$message.error(this.$t("VAR_FORMAT_ERR"));
          this.section = "";
        }
      } else {
        this.$message.success(this.$t("VAR_SET_EMPTY"));
        this.section = "";
      }
    },
    // 生成文档页面
    getDoc() {
      const docRef = this.$refs.printDoc2;
      /*
        提取对应dom节点的html标签和内容
        复制出三个dom节点，修改样式，插入到页面对应位置
       */
      let elHTML = this.$refs.print.outerHTML; //
      const dxml = document.getElementById("dxml");
      const dicon = document.getElementById("dicon");
      const noicon = document.getElementById("noicon");

      dxml.innerHTML = elHTML;
      dicon.innerHTML = elHTML;
      noicon.innerHTML = elHTML;
      let doc = docRef.outerHTML;
      return doc;
    },
    // 打印pdf
    print() {
      this.isLoading = true;
      const style = "./iconfonts/iconStyle.css";
      const style2 = "./iconfonts/quill.css";
      const scripts = [];

      let section = document.createElement("section");

      if (this.isGHP61) {
        section.innerHTML = this.getDoc();
      } else {
        if (this.section) {
          //已设置变量值
          section = this.section;
        } else {
          const led = document.getElementById("led");
          let elHTML = this.$refs.print.outerHTML;
          led.innerHTML = elHTML;
          section.innerHTML = this.getDoc();
        }
      }

      const printCallback = ({ launchPrint }) =>
        setTimeout(() => {
          launchPrint();
        }, 500);
      const d = new Printd();
      d.print(
        section,
        [style, style2, printCss, docCss, this.isGHP61 ? pageGHP1 : pageGHP2],
        scripts,
        printCallback
      );
      this.isLoading = false;
    },
  },
};
</script>
