<template>
  <div id="qeditor">
    <quill-editor
    ref="qeditor"
      v-model="content"
      :options="editorOption"
      @change="onEditorChange($event)"
    />
    
  </div>
</template>
<script>
import './index.scss'
import Quill from "quill"; // 引入编辑器
import { quillEditor } from "vue-quill-editor";
const markIcon=require('../../assets/roomNo.svg')
// 调整图片大小
import ImageResize from "quill-image-resize-module";
Quill.register("modules/imageResize", ImageResize);
// 自定义字体大小
const Size = Quill.import("attributors/style/size");
Size.whitelist = [
  "12px",
  "14px",
  "16px",
  "18px",
  "20px",
  "22px",
  "24px",
  "36px",
  "48px",
];
Quill.register(Size, true);


// 自定义字体样式
const Font = Quill.import('attributors/style/font')
const fonts = [
    'Open Sans',
    'Anton',
    'Playfair',
    'Asul',
]
Font.whitelist = fonts // 将字体加入到白名单
Quill.register(Font, true)


export default {
  name: "qeditor",

  components: {
    quillEditor,
  },
  mounted() {
            const Btn=document.querySelector('.ql-roomNo')
            const img=document.createElement('img')
            img.src=markIcon
            Btn.appendChild(img)
  },
  data() {
    return {
      content: ``,
      editorOption: {
        modules: {
          toolbar: {
              container: [
            ["bold", "italic", "underline", "strike"], // 加粗 斜体 下划线 删除线 -----['bold', 'italic', 'underline', 'strike']
            ["blockquote", "code-block"], // 引用  代码块-----['blockquote', 'code-block']
            [{ list: "ordered" }, { list: "bullet" }], // 有序、无序列表-----[{ list: 'ordered' }, { list: 'bullet' }]
            [{ header: 1 }, { header: 2 }],
            [{ script: "sub" }, { script: "super" }], // 上标/下标-----[{ script: 'sub' }, { script: 'super' }]
            [{ indent: "-1" }, { indent: "+1" }],
            [{ size: Size.whitelist, }, ], // 字体大小
            [{ header: [1, 2, 3, 4, 5, 6, false] }], // 标题-----[{ header: [1, 2, 3, 4, 5, 6, false] }]
            [{ color: [] }, { background: [] }], // 字体颜色、字体背景颜色-----[{ color: [] }, { background: [] }]
            [{ font: fonts }], //显示字体选择
            [{ align: [] }], // 对齐方式-----[{ align: [] }]
            ["clean"], // 清除文本格式-----['clean']
            ["image"], // 链接、图片、视频-----['link', 'image', 'video']
            ["roomNo"]// 新增工具补全对齐线
          ],
          handlers:{
              roomNo:this.roomNo
          }
          },

          //   调整图片大小
          imageResize: {
            displayStyles: {
              backgroundColor: "black",
              border: "none",
              color: "white",
            },
            modules: ["Resize", "DisplaySize", "Toolbar"],
          },
        },
      },
    };
  },
  created() {
      const editorHtml=localStorage.getItem('editorHtml')
      this.content=editorHtml
  },
  methods: {
    onEditorChange({ html }) {
      this.$emit("editor", html);
    },
      roomNo() {
          //清除所有之前被标记的元素
          const rooms=document.querySelectorAll('.room-no')
          rooms.forEach((el) => {
              el.classList.remove('room-no')
          })
        const {focusNode,baseOffset}=window.getSelection()
        if(!baseOffset) return this.$message.error('请选择需要标记的内容')
        const p=focusNode.parentNode
        p.className='room-no'
                const html=document.querySelector('.ql-editor').innerHTML
                this.$message.success('标记成功')
this.$emit("editor", html);
  }

  },
  
};
</script>
