
import Vue from 'vue'
import { Input, Checkbox,Button,Select,Option,Dialog,Message,Link,Loading,Backtop} from 'element-ui'
import VueI18n from 'vue-i18n'
import ElementLocale from 'element-ui/lib/locale'
import  lang from './lang.js'


Vue.use(Checkbox)
Vue.use(Input)
Vue.use(Button)
Vue.use(Select)
Vue.use(Dialog)
Vue.use(Link)
Vue.use(Option)
Vue.use(Loading)
Vue.use(Backtop)
Vue.prototype.$message = Message
// 全局注册组件

// 国际化
Vue.use(VueI18n)
const messages = lang

export const i18n = new VueI18n({
  locale: localStorage['locale'] || 'en',
  messages,
  silentTranslationWarn: true
})

ElementLocale.i18n((key, value) => i18n.t(key, value))


